import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { SlLogin, SlPencil  } from "react-icons/sl";
import '../Css/Common.css';

function TopNav() {
    return (
        <header>
            <div className="hd_top">
                <span className="logo"><a href="/main">My Recipe</a></span>
                    <Form className="d-flex">
                        <Form.Control
                            type="search"
                            placeholder="Search"
                            className="me-2"
                            aria-label="Search"
                        />
                        <Button className="btn_regular" variant="outline-success">Search</Button>
                    </Form>
                <ul className="signInUp">
                    <li><a href="/signin"><SlPencil /> </a></li>
                    <li><a href="/login"><SlLogin /> </a></li>
                </ul>
            </div>

            <Navbar expand="lg" className="bg-body-navbar">
                <Container fluid id="contNav">
                    <Navbar.Toggle aria-controls="navbarScroll" />
                    <Navbar.Collapse id="navbarScroll">
                        <Nav
                            className="navbar-text"
                            style={{maxHeight: '100px'}}
                            navbarScroll
                        >
                            <NavDropdown title="" id="navbarScrollingDropdown" className="menulist">
                                <NavDropdown.Item href="#action1">
                                     |  | // |  | / | // |  | / | /// |  |  |  | / | // | </NavDropdown.Item>
                                <NavDropdown.Item href="#action2"></NavDropdown.Item>
                                <NavDropdown.Item href="#action3"></NavDropdown.Item>
                            </NavDropdown>
                            <Nav.Link href="#action4" className="menulist"> </Nav.Link>
                            <Nav.Link href="#action5" className="menulist"> </Nav.Link>
                            <Nav.Link href="#action6" className="menulist"></Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>

        </header>
    );
}

export default TopNav;